"use client";

import { cn } from "@/lib/util";
import * as CheckboxPrimitive from "@radix-ui/react-checkbox";
import { CheckIcon } from "@radix-ui/react-icons";
import * as React from "react";

const Checkbox = React.forwardRef<
   React.ElementRef<typeof CheckboxPrimitive.Root>,
   React.ComponentPropsWithoutRef<typeof CheckboxPrimitive.Root>
>(({ className, ...props }, ref) => (
   <CheckboxPrimitive.Root
      ref={ref}
      className={cn(
         "peer h-4 w-4 shrink-0 rounded-sm  border-gray-300 focus:ring-purple-500 border shadow focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 data-[state=checked]:bg-primary data-[state=checked]:text-primary-foreground",
         className,
      )}
      {...props}
   >
      <CheckboxPrimitive.Indicator
         className={cn("flex items-center justify-center text-current bg-purple-600 rounded")}
      >
         <CheckIcon className="h-4 w-4" />
      </CheckboxPrimitive.Indicator>
   </CheckboxPrimitive.Root>
));
Checkbox.displayName = CheckboxPrimitive.Root.displayName;

export { Checkbox };
